import React from 'react'
import Header from './Header'
import Footer from './Footer'
import Contact from './Contact'

const cc = [
 
  {
    name: "Mr. Fluffykins",
    imgUrl:
      "https://cdn.pixabay.com/photo/2019/01/03/19/24/landscape-3911734__340.jpg",
    phone: "080800",
    email: "mrfluffykinsn@g.com",
  },
  {
    name: "Mr. Wiskerson",
    imgUrl:
      "https://cdn.pixabay.com/photo/2021/07/08/11/48/kangaroo-6396706_960_720.jpg",
    phone: "09009090",
    email: "mrwiskerson@g.com",
  },
  {
    name: "Mr. Wiskerson",
    imgUrl:
      "https://cdn.pixabay.com/photo/2021/07/08/11/48/kangaroo-6396706_960_720.jpg",
    phone: "09009090",
    email: "mrwiskerson@g.com",
  },
 ];


 const AllContacts = () => {
  return cc.map((item) => {
    return (
      <Contact
        contact={{
          key:item,
          name: item.name,
          imgUrl: item.imgUrl,
          phone: item.phone,
          email: item.email,
        }}
      />
    );
  });
 };
  

function App() {
  return (
    <div>
      <Header />
      <AllContacts />
      <Footer />
 
</div>
  )
}

export default App


